import { Component, OnInit , Inject, Renderer2, ViewChild, OnDestroy  } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import {EmployeeService} from '../../services/employee/employee.service';
import {AuthService} from '../../services/auth/auth.service';
import {ConfigurationService} from '../../services/configuration/configuration.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { DataSource } from '@angular/cdk/table';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { MatDatepicker } from '@angular/material/datepicker';
import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';
import { animate, state, style, transition, trigger } from '@angular/animations';

@Component({
  selector: 'app-decaissement',
  templateUrl: './decaissement.component.html',
  styleUrls: ['./decaissement.component.scss'],
  providers: [
    {provide: MAT_DATE_LOCALE, useValue: 'fr'},
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    {provide: MAT_DATE_FORMATS, useValue: MAT_MOMENT_DATE_FORMATS},
  ],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0', visibility: 'hidden'})),
      state('expanded', style({height: '*', visibility: 'visible'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class DecaissementComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator1: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort1: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  MyDataSource1: any;
  MyDataSource: any;
  fours = [];
  decs = [];
  banques = [];
  form : FormGroup;
  decForm : FormGroup;
  id_Four :any;
  id_Dec : any;
  isForm = true ;
  expandedElement:  null;
  counter = 0;
  displayedColumns1: string[] = ['nom','mat_fis', 'adresse','action'];
  displayedColumns: string[] = ['lib','nature','four','date','type','action'];
  dateDeb : any;
  dateFin : any;
  natures = ['Achats stockés - Matières premières et fournitures liées','Achats stockés - Autres approvisionnements','Matières consommables','Fournitures consommables','Achats d’études et de prestations de services','Achats de matériel, équipements et travaux','Achats non stockés de matières et fournitures','Sous-traitance générale',"Redevances pour utilisation d'immobilisations concédées",'Locations','Charges locatives et de copropriété','Entretien et réparations',"Primes d'assurances","Études, recherches et divers services extérieurs","Personnel extérieur à l'entreprise"," Rémunération d'intermédiaires et honoraires","Publicité, publications, relations publiques","Transports de biens et transports collectifs du personnel","Transports sur achats","Transports sur ventes","Transports administratifs","Transports collectifs du personnel","Divers","Déplacements, missions et réceptions","Voyages et déplacements","Frais de déménagement","Missions","Réceptions","Frais postaux et frais de télécommunications","Autres frais et commissions sur prestations de services"];
  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    private employeeService: EmployeeService,
    private cService : ConfigurationService,
    private snackBar: MatSnackBar,
    public authservice: AuthService,
    private fb: FormBuilder,
  ) {

    this.form = this.fb.group({
      name: ['', Validators.required],
      adresse: ['',Validators.required],
      mat_fis :[''],
    });

    this.decForm = this.fb.group({
      date: ['', Validators.required],
      lib: ['',Validators.required],
      amount: ['',Validators.required],
      nature: ['',Validators.required],
      four :['',Validators.required],
      payment:'',
      type:new FormControl('2',Validators.required),
      numero: [''],
      datePay: [''],
      payd : 0,
      bank : '',
      myBank : '',
      ref : '',
    });
   }

   ngOnDestroy(): void {
    if(this.authservice.introJS){
      this.renderer.removeClass(this.document.body, 'embedded-body-treso');
     }
  }

  ngOnInit() {
    if(this.authservice.introjs){
      this.renderer.addClass(this.document.body, 'embedded-body-treso');
      setTimeout(() =>{
        
        this.authservice.introJS.goToStep(20).start();
      }, 500);     
    }
    this.getBanques();
    this.fetchFour();
    this.fetchDec();
  }


  applyFilterFour(filterValue){
    this.MyDataSource1.filter = filterValue.trim().toLowerCase();
  }

  applyFilter(filterValue){
    this.MyDataSource.filter = filterValue.trim().toLowerCase();
  }


  getMoneyFormat(data){
    return Number(data.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
  }

  numericOnly(event): boolean {
    const patt =  /^\d*\.?\d*$/;
    return patt.test(event.key);
  }

  estwi() {
     this.decForm.controls.amount.patchValue(Number(this.decForm.controls.amount.value.toString().replace(/\s/g, '')).toFixed(3).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 '));
    }

    stringAsDate(dateStr: string) {
      return new Date(dateStr);
    }

    checkCash(){
      return Number(this.decForm.controls.amount.value.toString().replace(/\s/g, '')) <= 5000 ;
     }


     checkDates(){
      if(this.dateDeb && this.dateFin){
        let x = this.decs.filter(service => new Date(service.date) >= this.dateDeb &&  new Date(service.date) <= this.dateFin );
        this.MyDataSource = new MatTableDataSource(x); 
      }
     }

  deleteFour(){
    if(this.id_Four){
      this.employeeService.deleteFour(this.id_Four)
      .subscribe(res => {
        this.fetchFour();
        this.isForm = true;
        this.form.reset();
        this.id_Four = '';
        this.snackBar.open('Fournisseur supprimé', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        }, (err) => {
          console.log(err);
        }
      );
    }
  }

  createFour(){
    if(this.id_Four) {
     this.employeeService.updateFour(this.id_Four, this.form.value).subscribe(data => {
      if (data.success) {
        this.fetchFour();
        this.isForm = true;
        this.form.reset();
        this.id_Four = '';
        this.snackBar.open('Mise à jours avec succès', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
      } else {
        console.log(data);
        this.snackBar.open('Une erreur s\'est produite', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
      }
     });
    } else {
      this.employeeService.createFour(this.form.value).subscribe(data => {
        if (data.success) {
          this.fetchFour();
          this.isForm = true;
          this.form.reset();
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        } else {
            this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
        }
       });
    }
  }


  /// create dec 
  createDec(){
   let payment ={
    type: this.decForm.controls.type.value,
    numero: this.decForm.controls.numero.value,
    date: this.decForm.controls.datePay.value,
    bank : this.decForm.controls.bank.value,
    ref : this.decForm.controls.ref.value,
    payd :  this.decForm.controls.payd.value,
    myBank : this.decForm.controls.myBank.value,

    }
    this.decForm.controls.amount.patchValue(Number(this.decForm.controls.amount.value.toString().replace(/\s/g, '')));
    this.decForm.controls.payment.patchValue(payment);
    console.log(this.decForm.value);
    if(this.id_Dec) {
      console.log(this.decForm.value);
     this.employeeService.updateDec(this.id_Dec, this.decForm.value).subscribe(data => {
      if (data.success) {
        this.fetchDec();
        this.form.reset();
        this.id_Dec = '';
        this.snackBar.open('Mise à jours avec succès', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
      } else {
        console.log(data);
        this.snackBar.open('Une erreur s\'est produite', 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
      }
     });
    } else {
      this.employeeService.createDec(this.decForm.value).subscribe(data => {
        if (data.success) {
          this.fetchDec();
          this.form.reset();
          this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],
         });
        } else {
            this.snackBar.open(data.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
        }
       });
    }
  }

  setFour(id){
    this.isForm = false;
    this.id_Four = id;
    let four = this.fours.find(one => one._id == id);
    this.form.patchValue({
      name : four.name,
      mat_fis :four.mat_fis,
      adresse : four.adresse
    })
  }

  setDec(id){
    this.id_Dec = id;
    let dec = this.decs.find(one => one._id == id);
    dec.amount = this.getMoneyFormat(dec.amount);
    this.decForm.patchValue(dec);
    if(dec.payment){
    this.decForm.patchValue({
      datePay : dec.payment.date,
      bank : dec.payment.bank,
      type : dec.payment.type,
      ref : dec.payment.ref,
      payd : dec.payment.payd,
      numero : dec.payment.numero,
      myBank : dec.payment.myBank,
    })
  }
  }
  fetchFour(){
    this.employeeService.getAllFour()
    .subscribe(res => {
    this.fours = res.filter(four => four.active) ;
    this.MyDataSource1 = new MatTableDataSource(this.fours);
    this.MyDataSource1.paginator = this.paginator1;
    this.MyDataSource1.sort = this.sort1;
    }, err => {
      console.log(err);
    });
    
  }

  getBanques(){
    this.cService.getAllBanques()
    .subscribe(res => {
    this.banques = res ;
    }, err => {
      console.log(err);
    });
    
  }

  fetchDec(){
    this.employeeService.getAllDec()
    .subscribe(res => {
    this.decs = res ;
    this.MyDataSource = new MatTableDataSource(this.decs);
    this.MyDataSource.paginator = this.paginator;
    this.MyDataSource.sort = this.sort;
    }, err => {
      console.log(err);
    });
    
  }

}
